  position: absolute;
  left: 50%;
  top: 59%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.admin-dashboard-icons > ul {
  position: relative;
  width: 100%;
  overflow: hidden;
  list-style: none; }

.admin-dashboard-icons > ul > li {
  position: relative;
  float: left;
  margin: 15px;
  border-radius: 20%;
  overflow: hidden;
  height: 120px; }

.icon-eleme span {
  font-size: 14px; }

.admin-dashboard-icons > ul > li > .icon-eleme {
  display: inline-block;
  border-radius: 50%;
  overflow: hidden; }

.admin-dashboard-icons > ul > li > .icon-eleme > a {
  color: white;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  height: 135px;
  width: 135px; }

.admin-dashboard-icons > ul > li:hover i {
  -webkit-transition: 0.9s;
  transition: 0.9s;
  -webkit-transform: rotateZ(360deg);
          transform: rotateZ(360deg); }

