tbody + thead {
  display: none;
}
.table thead,
.table th {
  text-align: center;
  font-weight: bold;
  font-size: 12px !important;
}
/*universal-table-style*/
.table td {
  text-align: left;
  font-size: 12px;
  vertical-align: middle !important;
}
/*end universal table style*/
/*viewpurchaseorder*/
.viewpurchaseorder .table td {
  text-align: center;
}
.viewpurchaseorder .table td:nth-child(6) {
  text-align: left;
  font-size: 12px !important;
}
.viewpurchaseorder .table td:nth-child(3) {
  text-align: left;
  font-size: 12px !important;
}
/*endviewpurchaseorder*/
/*masteritemlist*/
.masteritemlist .table td:nth-child(2){
  text-align: center;
}
.masteritemlist .table td:nth-child(3) {
  max-width: 350px;
  word-wrap: break-word;
}
.masteritemlist .table td:nth-child(5){
  text-align: center;
}
.masteritemlist .table td:nth-child(6){
  text-align: center;
}
/*end masteritemlist*/
/*supplierlist*/
.supplierlist .table td:nth-child(1) {
  text-align: center;
}
.supplierlist .table td:nth-child(5) {
  text-align: center;
}
/*end supplierlist*/


/*total quantity*/
.totalquantity {
  width: 80%;
}
.totalquantity .table td:nth-child(1),
.totalquantity .table td:nth-child(3){
  text-align: center;
  width: 50px;
}

.totalquantity .table tr th:nth-child(2),
.totalquantity .table tr td:nth-child(2) {
  width: 150px;
}
.totalquantity .table tr th:nth-child(4),
.totalquantity .table tr td:nth-child(4) {
  width: 150px;
}
/*end total quantity*/

/* Start viewproductlist */
.viewproductlist .table td:nth-child(2) {
  text-align: center;
}
/* End viewproductlist */

/* Start customerlist */
.customerlist .table td:nth-child(1) {
  text-align: center;
}
.customerlist .table td:nth-child(8) {
  text-align: center;
}
.customerlist .table td:nth-child(11) {
  text-align: center;
}
/* Start customerlist */

/*Start quotationlist */
.quotationlist .table td {
  text-align: center;
}
.quotationlist .table td:nth-child(2) {
  text-align: left;
}
/*End quotationlist */

/* Start invoicelist */
.invoicelist .table td:nth-child(1),
.invoicelist .table td:nth-child(3),
.invoicelist .table td:nth-child(4),
.invoicelist .table td:nth-child(5) {
  text-align: center;
}
/* End invoicelist */

/* Start challanlist */
.challanlist .table td{
  text-align: center;
}
/* End challanlist */

/* Start orderlist */
.orderlist .table td{
  text-align: center;
}
.orderlist .table td:nth-child(2){
  text-align: left;
}
/* End orderlist */

/* Start productionlist */
.productionlist .table td{
  text-align: center;
}
.productionlist .table td:nth-child(4){
  text-align: left;
}
/* End productionlist */

/* Start moveorderlist */

.moveorderlist .table td:nth-child(1){
  text-align: center;
}
/* End productionlist */
